.app__form {
    position: fixed;
    bottom: 0;
    padding: 20px;
    margin: 20px;
    z-index: 1;
    background-color: #e9e9eb;
    width: calc(100% - 80px);
}

.app__formControl {
    display: flex !important;
    flex-direction: row !important;
}

.app__input {
    flex: 1;
}

.app__iconButton {
    flex: 0;
}
